#root {
  max-height: 100%;
  width: 100%;

  display: flex;
}

.bg {
  transition: background var(--transition-short-ms);
}

.bg[color-theme="light"] {
  background-image: var(--background-light-orange-light-pink-1);
  background-position: center;
  background-repeat: no-repeat;
}

.bg[color-theme="dark"] {
  background-image: var(--background-dark-purple-lighter-purple-1);
  background-position: center;
  background-repeat: no-repeat;
}

.bg-l-1 {
  background-image: var(--background-light-orange-light-pink-1);
  background-position: center;
  background-repeat: no-repeat;
}

.bg-d-1 {
  background-image: var(--background-dark-purple-light-blue-1);
  background-position: center;
  background-repeat: no-repeat;
}